.earth_container {
  position: absolute;
  top: -40px;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1; }
  .earth_container canvas {
    background-image: url("../images/bg_meteor.png"), url("../images/bg_meteor.png"), url("../images/bg_meteor.png");
    background-repeat: no-repeat;
    background-size: 64px;
    -webkit-animation: meteorMove 10s linear infinite;
            animation: meteorMove 10s linear infinite;
    width: 375px;
    height: 600px;
    position: relative; }

@-webkit-keyframes meteorMove {
  0% {
    background-position: 1040px 0,1240px -100px,300px -100px; }
  20% {
    background-position: -10px 1000px,1240px -100px,300px -100px; }
  50% {
    background-position: -10px 1040px,-100px 800px,300px -100px; }
  70% {
    background-position: -10px 1040px,-100px 800px,300px -100px; }
  90% {
    background-position: -10px 1040px,-100px 800px,-300px 600px; }
  100% {
    background-position: -10px 1040px,-100px 800px,-300px 600px; } }

@keyframes meteorMove {
  0% {
    background-position: 1040px 0,1240px -100px,300px -100px; }
  20% {
    background-position: -10px 1000px,1240px -100px,300px -100px; }
  50% {
    background-position: -10px 1040px,-100px 800px,300px -100px; }
  70% {
    background-position: -10px 1040px,-100px 800px,300px -100px; }
  90% {
    background-position: -10px 1040px,-100px 800px,-300px 600px; }
  100% {
    background-position: -10px 1040px,-100px 800px,-300px 600px; } }

.show_container {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0; }
  .show_container .page_container {
    position: absolute;
    display: inline-block;
    top: 50%;
    left: 50%;
    width: 375px;
    height: 600px;
    margin: -300px 0 0 -187.5px; }
    .show_container .page_container .page_show {
      position: relative;
      width: 100%;
      height: 100%;
      -webkit-transform: translateY(0px);
          -ms-transform: translateY(0px);
              transform: translateY(0px);
      -webkit-transition: -webkit-transform 1s ease;
      transition: -webkit-transform 1s ease;
      -o-transition: transform 1s ease;
      transition: transform 1s ease;
      transition: transform 1s ease, -webkit-transform 1s ease; }



